/* header showcase */

#showcase{
    min-height: 700px;
    color: #F0F2F5;
    text-align: center;
    background:  url(/static/media/art4.7fe15045.png);background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: auto;
    
}

#showcase h1{
    padding-top: 2em;
    padding-bottom: 0%;
    
}
#showcase .content-wrap,
#showcaseOne .content-wrap,
#section-a .content-wrap{
    padding: 0 1.5em;
}

#showcaseOne{
    min-height: 700px;
    color: #F0F2F5;
    text-align: center;
}

#showcaseOne h1{
    padding-top: 1em;
    padding-bottom: 0%;
}
#showcaseOne .content-wrap{
    height: 100%;
    width: auto;
    grid-column: 1;
    display: inline-block;
}
/* section A */

#section-a {
   
    background: rgb(100, 125, 161) ;
    padding-bottom: 2em;
}
/* form */
#swapper-first{
    display:block;
}
#swapper-other{
    display:none;
}
.sfclass{
    display:block; 
    text-align:center;
}
.soclass{
    display:none;
    text-align:center;
    box-sizing: border-box;
}
/* section B */

#section-b {
    padding: 2em 1em 1em;
}
#section-b ul{
    list-style: none;
    margin: 0%;
    padding: 0%;
}
#section-b li{
    margin-bottom:  1em;
    background: #606E8A;
    color: #EBECF0;
}
