    padding: 1.5em;
    box-shadow:0 15px 25px  rgba(0, 0, 0, .4);
    height: 100%;
}
.card img{
    box-shadow:0 15px 25px  rgba(0, 0, 0, .6);
}

/* section C */

#section-c {
    background: #EBECF0;
    color: #36496D;
    padding: 2em;
}
#section-ca {
    background: rgb(20, 26, 49);
    color: #36496D;
    padding: 2em;
}
#section-ca h2{
    font-size: 2em;
    color: white;
}
#section-cdos {
    background: #4B5E81;
    padding: 2em;
}
#section-ctres{
    background:#737373;
    color: #0D305F;
}
.content-wrap #contactform{
    grid-column: 1;
    display: inline-block;
}

/* section D / Boxes  */
 #section-d .box{
     padding: 2em;
     color: #EBECF0;
 }
 #section-d .box:first-child{
     background: rgb(2, 14, 39);
 }

 /* Footer */

 #main-footer {
     padding: 3em;
     background: black;
     color: #F0F2F5;
     text-align: center;
 }
 #main-footer a{
     color: #889DC8;
     text-decoration: none;
 }

 /* Media Querie */
 @media(max-width: 920px) {
     .boxCon{
         padding: 0em;
     }
     .boxCon label {
         font-size: 1em;
     }
     .boxCon input {
         font-size: 0.8em;
     }
     textarea{
        font-size: 0.8em;
        border-radius: 1em;
     }
     #section-ctres form .btn {
         margin-bottom: 10%;
     }
