    }
   
 }
 @media(min-width: 700px) {
     .grid{
         display: grid;
         grid-template-columns: 1fr repeat(2, minmax(auto, 25em)) 1fr;
     }

     #section-a .content-text{
         -webkit-columns: 2;
                 columns: 2;
         grid-column-gap: 2em;
         -webkit-column-gap: 2em;
                 column-gap: 2em;
     }
     #section-a .content-text p{
         padding-top: 0%;
     }

     .content-wrap,
     #section-b ul{
         grid-column: 2/4;
     }
     .box, #main-footer div{
         grid-column: span 2;
     }
     #section-b ul{
         display: -webkit-flex;
